@import url(https://fonts.googleapis.com/css?family=Source+Code+Pro&display=swap);
@import url(https://fonts.googleapis.com/css?family=Source+Code+Pro&display=swap);

/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
html {
  background-color: black;
}
.App {
  font-family: 'Source Code Pro', monospace;  
  color: white;
  height: 90vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.App-container {
  border: white solid 2px;
  width: 50%;
  height: 80%;
  padding: 1em;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
}

.input-box {
  color: white;
  font-family:'Source Code Pro', monospace;
  background-color: transparent;
  border: none;
  outline: none;
  border-bottom: 1px solid white;
  font-size: 1em;
  padding-bottom: .5em;
}

button {
  font-family:'Source Code Pro', monospace;
  border-radius: 5px;
}

button:hover {
  cursor: pointer;
  background-color: rgb(238, 235, 235);
  border-radius: 5px;
}

@media (max-width: 450px) {
  .App-container {
    width: 75%;
    height: 83%;
  }
}
.swal-modal {
    background-color: #0F1922 !important;
}
  
.swal-icon--success::before,.swal-icon--success::after  {
background-color: #0F1922 !important;
}

.swal-icon--success__hide-corners {
background-color: #0F1922 !important;
}

.swal-overlay {
    background-color: rgb(83, 92, 100, 0.6);
}

.swal-title {
    font-family: 'Source Code Pro', monospace;
    color: white;
}

.swal-text, .swal-content, .swal-content a {
    font-family: 'Source Code Pro', monospace;
    color: white;
    word-wrap: break-word;
    text-align: center;
    display: block;
    /* text-decoration: none; */
}

.swal-button {
    font-family: 'Source Code Pro', monospace;
    outline: none;
    padding: 7px 19px;
    border-radius: 2px;
    background-color: rgb(83, 92, 100);
    font-size: 12px;
    border: none;
}

.swal-button:hover {
    outline: none;    
    padding: 7px 19px;
    border-radius: 2px;
    background-color: rgb(83, 92, 100);
    font-size: 12px;
    border: none;
}

.swal-button:focus {
    box-shadow: none;
}

.swal-button:not([disabled]):hover {
    background-color: rgb(57, 63, 68);
}
